.WhiteDiv {
    background-color: white;
    display: flex;
    flex-direction: row;
    width: 500px;
    height: 600px;
    margin: 0px auto;
    margin-top: 50px;
    justify-content: center;
    /* 水平居中 */
    align-items: center;
    /* 垂直居中 */
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);

    h1 {
        color: var(--Green);
        font-size: 72px;
    }
}


@media screen and (max-width: 500px) {
    .WhiteDiv {

        width: 400px;
        height: 400px;
        justify-content: center;
        /* 水平居中 */
        align-items: center;
        /* 垂直居中 */
        margin: 0px auto;
        margin-top: 50px;


    }

    .WhiteDiv h1 {
        font-size: 48px;

    }
}